import React,{Component} from 'react';
import './Headings.css'
class Headings extends Component{
	render(props){
		return(
			<div id="1hello" className="bc">
			<div id="tree" className="headings">{this.props.name}<br/></div>
			<div className="des"><h9>{this.props.description}</h9><br/></div>
			<br/><br/></div>
			)
	}
}
 
 export default Headings;