import React, { Component } from 'react';
  class SimpleMap extends Component{
    constructor(){
      super();
      this.state={
        isLoggedIn: true
      }
    }
  render(){
    let wordDisplay
    if(this.state.isLoggedIn)
    {
      wordDisplay="in"
    }
    else
    {
      wordDisplay="out"
    }
    return (
      
     <div>
     {wordDisplay}
<button onClick={()=>alert("clicked")}/>
     </div>
    )
  }
}
 
export default SimpleMap;